'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { TrendingUp, Users, Heart, Zap, RefreshCw, Activity, MessageSquare, Send, Target } from 'lucide-react';
import { useAgentStore } from '@/lib/store';

// Modular Components
import StatsCard from '@/components/dashboard/StatsCard';
import ActivityFeed, { ActivityItem } from '@/components/dashboard/ActivityFeed';
import AnalyticsChart from '@/components/dashboard/AnalyticsChart';

export default function DashboardPage() {
    const { stats, syncStatus, agentStatus } = useAgentStore();
    const [activity, setActivity] = useState<ActivityItem[]>([]);
    const [weeklyData, setWeeklyData] = useState<any[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        syncStatus();
        fetchData();
        const interval = setInterval(() => { syncStatus(); fetchData(); }, 10000);
        return () => clearInterval(interval);
    }, []);

    const fetchData = async () => {
        try {
            const [activityRes, chartRes] = await Promise.all([fetch('/api/activity'), fetch('/api/charts/weekly')]);
            if (activityRes.ok) {
                const data = await activityRes.json();
                setActivity(data.recent || []);
            }
            if (chartRes.ok) {
                const data = await chartRes.json();
                setWeeklyData(data.data || []);
            }
        } catch {
            // Mock Fallback
            setActivity([
                { id: 1, action: 'Liked post', target: 'John Smith', time: '2 min ago', type: 'like' },
                { id: 2, action: 'Commented', target: 'Sarah Johnson', time: '5 min ago', type: 'comment' },
                { id: 3, action: 'Connection sent', target: 'Mike Chen', time: '10 min ago', type: 'connection' },
            ]);
            setWeeklyData([
                { day: 'Mon', likes: 45, comments: 12, connections: 8 },
                { day: 'Tue', likes: 52, comments: 18, connections: 12 },
                { day: 'Wed', likes: 38, comments: 15, connections: 6 },
                { day: 'Thu', likes: 65, comments: 22, connections: 15 },
                { day: 'Fri', likes: 48, comments: 14, connections: 10 },
                { day: 'Sat', likes: 30, comments: 8, connections: 4 },
                { day: 'Sun', likes: 25, comments: 6, connections: 3 },
            ]);
        }
        setIsLoading(false);
    };

    const runningAgents = Object.entries(agentStatus).filter(([_, status]) => status === 'running').length;

    return (
        <div className="space-y-4 md:space-y-6 p-4 md:p-6 pb-20">
            {/* Header */}
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3 mb-2">
                <div>
                    <h1 className="text-xl md:text-3xl font-bold bg-clip-text text-transparent bg-linear-to-r from-white to-gray-400">
                        Dashboard
                    </h1>
                    <p className="text-gray-400 text-sm mt-0.5 hidden sm:block">
                        LinkedIn automation overview
                    </p>
                </div>

                <div className="flex items-center gap-2 md:gap-3">
                    {runningAgents > 0 && (
                        <motion.div
                            initial={{ opacity: 0, scale: 0.8 }}
                            animate={{ opacity: 1, scale: 1 }}
                            className="flex items-center gap-2 px-3 py-1.5 bg-green-500/10 border border-green-500/30 rounded-full text-xs md:text-sm shadow-lg shadow-green-900/20"
                        >
                            <div className="w-2 h-2 rounded-full bg-green-500 animate-pulse" />
                            <span className="text-green-400 font-medium">{runningAgents} Running</span>
                        </motion.div>
                    )}
                    <button
                        onClick={fetchData}
                        className="flex items-center gap-1.5 px-3 py-1.5 glass-card rounded-lg text-xs md:text-sm hover:bg-white/5 text-gray-300 hover:text-white transition-all"
                    >
                        <RefreshCw className={`w-3.5 h-3.5 ${isLoading ? 'animate-spin' : ''}`} />
                        Refresh
                    </button>
                </div>
            </div>

            {/* Stats Overview */}
            <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 md:gap-4">
                <StatsCard
                    label="Total Leads"
                    value={stats.leadsProcessed || 156}
                    icon={Users}
                    color="from-blue-500 to-cyan-500"
                    trend="+12%"
                    sparklineData={[10, 15, 12, 18, 22, 25, 28]}
                    delay={0}
                />
                <StatsCard
                    label="Engaged Posts"
                    value={stats.postsEngaged || 423}
                    icon={Heart}
                    color="from-pink-500 to-rose-500"
                    trend="+8%"
                    sparklineData={[30, 35, 28, 42, 38, 45, 50]}
                    delay={0.1}
                />
                <StatsCard
                    label="Connections"
                    value={stats.connectionsAccepted || 89}
                    icon={Zap}
                    color="from-amber-500 to-orange-500"
                    trend="+15%"
                    sparklineData={[5, 8, 12, 10, 15, 18, 22]}
                    delay={0.2}
                />
                <StatsCard
                    label="Success Rate"
                    value={stats.successRate || 67.8}
                    suffix="%"
                    icon={TrendingUp}
                    color="from-green-500 to-emerald-500"
                    trend="+2.1%"
                    sparklineData={[55, 58, 62, 60, 65, 64, 68]}
                    delay={0.3}
                />
            </div>

            {/* Main Content Areas */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 md:gap-6">

                {/* Main Chart Section - Spans 2 cols on Desktop */}
                <div className="lg:col-span-2 space-y-4 md:space-y-6 h-[400px]">
                    <AnalyticsChart data={weeklyData} delay={0.4} />
                </div>

                {/* Side Panel - Activity & Daily Stats */}
                <div className="space-y-4 md:space-y-6 h-full flex flex-col">
                    {/* Mini Stats Grid */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.5 }}
                        className="glass-card rounded-xl p-4 md:p-6 border border-white/5"
                    >
                        <div className="flex items-center justify-between mb-4">
                            <h2 className="text-base font-semibold text-gray-200">Today's Pulse</h2>
                            <Activity className="w-4 h-4 text-gray-600" />
                        </div>
                        <div className="space-y-2">
                            {[
                                { label: 'Likes', value: 48, icon: Heart, color: 'text-rose-500' },
                                { label: 'Comments', value: 12, icon: MessageSquare, color: 'text-blue-500' },
                                { label: 'Sent', value: 8, icon: Send, color: 'text-orange-500' },
                                { label: 'Views', value: 24, icon: Target, color: 'text-purple-500' }
                            ].map((item, idx) => (
                                <div key={item.label} className="flex items-center justify-between p-2.5 bg-white/5 rounded-lg border border-transparent hover:border-white/5 transition-all">
                                    <div className="flex items-center gap-3">
                                        <item.icon className={`w-4 h-4 ${item.color}`} />
                                        <span className="text-sm text-gray-400">{item.label}</span>
                                    </div>
                                    <span className="font-semibold text-gray-200">{item.value}</span>
                                </div>
                            ))}
                        </div>
                    </motion.div>

                    {/* Recent Activity Feed */}
                    <div className="flex-1 min-h-[300px]">
                        <ActivityFeed activities={activity} delay={0.6} />
                    </div>
                </div>
            </div>
        </div>
    );
}
